.card {
    height: 400px;
    width: 400px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 3px solid var(--color-black);
    border-radius: 10px;
    margin: 30px;
    transition: 0.2s all;
    cursor: pointer;
    color: var(--color-black);
    font-size: 1.5rem;
    text-decoration: none;
  }
  
  .card:active {
    transform: scale(0.98);
    box-shadow: 3px 2px 22px 1px var(--color-shadow);
  }
  
  .card__title {
    margin: 15px 0px;
  }